.range-slider-ui {
  height: var(--ar-range-slider-height);
  margin-top: 3.25rem;
  margin-right: 0;
  margin-bottom: 1.75rem;
  margin-left: 0;
  background-color: var(--ar-range-slider-bg);
  border: 0;
  box-shadow: none;
}
